@import '../App.css';

.navigator{
    position: sticky;
    top: 0;
    left: 0;
    width: 100vw;
    min-height: 3.8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--font-color);
}

.logo{
    user-select: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-size: 1.5rem;
    font-weight: bold;
    margin-left: 2rem;
}

.menus{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.menus div{
    height: 2.4rem;
    width: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
    user-select: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    border: 2px solid var(--font-color);
    border-radius: 5px;
    font-weight: bold;
}
.menus div:active{
    transform: scale(0.96);
}

@media screen and (max-width:1109px){
    .navigator{
        min-height: 3.8rem;
    }
    .logo{
        margin-left: 1.5rem;
    }
    .navigator i{
        margin-right: 1.5rem;
    }
}