:root{
  --body-background:#1E1E1E;
  --font-color: #E5E5E5;
  --description: #A3A3A3;
  
  --popup-back: #1d2b2e;
  --popup-input: #273c41;

  --task-col: #3D3D3D;
}
[data-theme="light"]{
  --body-background:#FFFFFF;
  --font-color: #000000;
  --description: #707070;

  --popup-back: #88a2a7;
  --popup-input: #afced6;

  --task-col: #E5E5E5;
} 
.App {
  max-width: 100vw;
  max-height: 100vh;
  min-height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: var(--body-background);
  display: flex;
  flex-direction: column;
}